<template>
	<ul class="slider">
		<li class="slider__item fade-in"
		:class="{'fade-out' : hide}"
		>
			<p class="slider__title">
				SNKRS OF<br />
				THE WEEK
			</p>
			<img
				class="slider__img"
				v-bind:src="require('../assets/images/slider/' + imgSlider + '.webp')"
			/>
			<p class="slider__label"><span>New Collection</span>Spring</p>
			<p class="slider__label-new">new</p>
			<div class="slider__btn"><baseButtonSecond /></div>
		</li>
	</ul>
</template>

<script>
import baseButtonSecond from "./base-button-second";
export default {
	components: {
		baseButtonSecond,
	},
	data() {
		return {
			imgSlider: 1,
			hide: false,
		}
	},
	mounted() {
		const changeSlide = (par) => {
			if (par == 1) return 2;
			return 1;
		}
		setInterval(() => {
			this.hide = true;
			setTimeout(() => {
				this.imgSlider = changeSlide(this.imgSlider);
				this.hide = false;
			}, 1000);

		}, 10000)
	}
};
</script>

<style lang="scss" scoped>
.slider {
	width: 100%;
	height: 100%;
	position: relative;
	&__item {
	}
	&__title {
		color: #ffffff;
		font-family: $poppinsSemiBold;
		font-size: 180px;
		font-weight: 700;
		line-height: 150px;
		text-transform: uppercase;
		letter-spacing: -18px;
		@include pos_50_50;
		top: 39%;
	}
	&__img {
		width: 52%;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 5;
	}
	&__label {
		overflow: hidden;
		position: absolute;
		top: 20%;
		left: 15%;
		width: 119px;
		color: #ffffff;
		font-family: $poppinsSemiBold;
		text-transform: uppercase;
		font-size: 24px;
		& span {
			display: block;
			font-size: 12px;
			font-family: $poppinsMedium;
			margin-bottom: 10px;
		}
	}
	&__btn {
		position: absolute;
		top: 75%;
		left: 15%;
		transform-origin: left;
	}
	&__label-new {
		position: absolute;
		top: 20%;
		left: 80%;
		display: block;
		background-color: #000;
		border-radius: 5px;
		color: #fff;
		padding: 3px 6px;
		font-family: $poppinsRegular;
		font-size: 11px;
	}
}
.fade-in.slider__item {
	& .slider {
		&__title {
			animation: titleFadeIn 1000ms both;
		}
		&__img {
			animation: imgFadeIn 1000ms both 500ms;
		}
		&__label {
			animation: labelFadeIn 500ms both 1250ms;
		}
		&__btn {
			animation: btnFadeIn 500ms both 1250ms;
		}
		&__label-new {
		}
	}
}

.fade-out.slider__item {
	& .slider {
		&__title {
			animation: titleFadeOut 500ms both 250ms;
		}
		&__img {
			animation: imgFadeOut 500ms both;
		}
		&__label {
			animation: labelFadeOut 500ms both;
		}
		&__btn {
			animation: btnFadeOut 500ms both;
		}
		&__label-new {
		}
	}
}

@keyframes imgFadeIn {
	0% {
		transform: translate(-50%, -35%);
		opacity: 0;
	}
	50% {
		opacity: unset;
	}
	100% {
		opacity: unset;

		transform: translate(-50%, -50%);
	}
}
@keyframes titleFadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: unset;
	}
}
@keyframes labelFadeIn {
	0% {
		transform: translateY(-40%);
		opacity: 0;
	}
	50% {
		opacity: unset;
	}
	100% {
		opacity: unset;
		transform: translateY(0);
	}
}
@keyframes btnFadeIn {
	0% {
		transform: translateY(40%);
		opacity: 0;
	}
	50% {
		opacity: unset;
	}
	100% {
		opacity: unset;
		transform: translateY(0);
	}
}
@keyframes titleFadeOut {
	0% {
		opacity: unset;
	}
	100% {
		opacity: 0;
	}
}
@keyframes imgFadeOut {
	0% {
		transform: translate(-50%, -50%);
		opacity: unset;
	}
	25% {
		opacity: unset;
	}
	100% {
		opacity: 0;
		transform: translate(-50%, -70%);
	}
}
@keyframes btnFadeOut {
	0% {
		transform:rotate(0deg) translate(0);
		opacity: unset;
	}
	25% {
		opacity: unset;
	}
	100% {
		
		opacity: 0;
		transform:rotate(15deg) translate(-100%,100%);
	}
}
@keyframes labelFadeOut {
	0% {
		transform:rotate(0deg) translate(0);
		opacity: unset;
	}
	25% {
		opacity: unset;
	}
	100% {
		
		opacity: 0;
		transform:rotate(-15deg) translate(-100%,-100%);
	}
}
</style>