import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		users: [],
		products: [
			{
				id: '',
				brand: '',
				style: '',
				title: '',
				options: {
					colors: [],
					sizes: [],
				},
				quanImg: 0,
				price: 0,
			},
		],
		count: 555,
		categories: [
			{
				name: '',
				styles: [],
				sizes: [],
			},
		],
		reviews: [],
		options: {},
		brands: [],
		cart: [{ productId: 'p001', quantity: 2, color: 'blue', size: 11, price: 250 },{ productId: 'p004', quantity: 5, color: 'blue', size: 11, price: 195}],
	},
	mutations: {
		increment(state) {
			state.count++
		},
		fetchToProducts(state, data) {
			state.products = data
		},
		fetchToUsers(state, data) {
			state.users = data
		},
		fetchToCategories(state, data) {
			state.categories = data
		},
		fetchToReviews(state, data) {
			state.reviews = data
		},
		fetchToOptions(state, data) {
			state.options = data
		},
		AddRevies(state, arr) {
			console.log(`arr - ${arr}`)
			console.log(`вставити у`)
			state.products[0].reviews = []
			state.products[0].reviews = arr
			console.log(state.products[0].reviews)
		},
		createBrandList(state) {
			state.products.forEach((element) => {
				if (!state.brands.includes(element.brand, 0)) state.brands.push(element.brand)
			})
		},
		cartChangeQuantity(state, obj) {
			state.cart[obj.index].quantity = obj.value;
		},
		cartProductRemove(state, index) {
			state.cart.splice(index, 1);
		},
		addToCart(state, product) {
			let res = state.cart.findIndex(el => el.productId == product.productId);
			if (res !== -1) {
				state.cart[res].quantity += product.quantity;
			} else {
				state.cart.push(product);
				console.log(state.cart);
			}
		}
	},
	actions: {
		fetchFromDB({ commit }) {
			Axios.get('http://localhost:3000/products')
				.then((res) => {
					commit('fetchToProducts', res.data)
					commit('createBrandList')
				})
				.catch((error) => console.log(error))
			Axios.get('http://localhost:3000/users')
				.then((res) => {
					commit('fetchToUsers', res.data)
				})
				.catch((error) => console.log(error))
			Axios.get('http://localhost:3000/categories')
				.then((res) => {
					commit('fetchToCategories', res.data)
				})
				.catch((error) => console.log(error))
			Axios.get('http://localhost:3000/reviews')
				.then((res) => {
					commit('fetchToReviews', res.data)
				})
				.catch((error) => console.log(error))
			Axios.get('http://localhost:3000/options')
				.then((res) => {
					commit('fetchToOptions', res.data)
				})
				.catch((error) => console.log(error))

			console.log('Run fetch to DB')
		},
		addProduct() {
			axios
				.post('http://localhost:3000/testPost', {
					firstName: 'Fred',
					lastName: 'Flintstone',
				})
				.then(function(response) {
					console.log(response)
				})
				.catch(function(error) {
					console.log(error)
				})
		},
	},
	getters: {
		PRODUCTS: (state) => {
			return state.products
		},
		USERS: (state) => {
			return state.users
		},
		ONEPRODUCT: (state) => {
			return state.product
		},
		PRODUCTGETID: (state) => (id) => {
			let res = state.products.filter((products) => products.id === id)
			return res[0]
		},
		PRODUCTSGETCATEGORY: (state) => (category) => {
			let res = state.products.filter((products) => products.category === category)
			return res
		},
		REVIEWSGETID: (state) => (id) => {
			let res = state.reviews.filter((reviews) => reviews.productid === id)
			return res
		},
		CATEGORIES: (state) => {
			return state.categories
		},
		SHOWCOUNT: (state) => {
			return state.count
		},
		USERNAMEFROMID: (state) => (id) => {
			let res = state.users.filter((user) => user.id === id)
			return res[0]
		},
		OPTIONS: (state) => {
			return state.options
		},
		BRENDS: (state) => {
			return state.brands
		},
		CART: (state) => {
			return state.cart
		},
	},
})

export default store
